.main-header {
    &_wrap {
        .row-flex();
        
    }
    &_top {
        background-color: #f5f5f5;
        padding: 8px 0;
        &--wrap {
            justify-content: space-between;
            align-items: center;
            .xs-block({
                flex-direction: column;
            });
        }
        &--regime {
            color: #616161;
            padding: 0 5px;
            .xs-block({
                padding: 10px 30px;
                text-align: center;
            });
        }
        &--mail {
            margin-left: 40px;
            padding-left: 20px;
            position: relative;
            color: #05acd0;
            font-size: 14px;
            text-decoration: underline;
            .xs-block({
                margin-left: 0;
            });
            &::after {
                content: '';
                position: absolute;
                top: 2px; left: 0;
                background: url(../img/mail.png) no-repeat;
                width: 15px;
                height: 13px;
            }
        }
        &--tel {
            padding: 0 5px;
            .row-flex();
            .lg-block({
                flex-direction: column;
            });
            .xs-block({
                margin-top: 10px;
            });
            .tel {
                margin: 0 5px;
                font-size: 18px;
                position: relative;
                padding-left: 20px;
                .lg-block({
                    padding-top: 7px;
                    padding-bottom: 10px;
                });
                &:hover {
                    text-decoration: underline;
                }
                &::after {
                    content: '';
                    background: url(../img/tel.png) no-repeat;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 18px;
                    height: 17px;
                    .lg-block({
                        top: 7px;
                    });
                }
            }
            .call-btn {
                position: relative;
                top: -2px;
                margin: 0 5px;
                margin-left: 20px;
                    background: url(../img/button.png) repeat-x center center;
                    border-radius: 7px;
                    color: #fff;
                    padding: 3px 15px;
                    border: #f1631d 1px solid;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
        &--reg {
            .xs-block({
               order: -1;
                padding: 5px 0;
            });
            a {
                border-left: 1px solid #e0e0e0;
                border-right: 1px solid #e0e0e0;
                color: #00647a;
                padding: 0 12px;
                &:hover {
                    text-decoration: underline;
                }
                &:first-child {
                    border-right: none;
                }
            }
        }
    }
    &_bottom {
        &--wrap {
            align-items: center;
            .lg-block({
                justify-content: center;
            });
        }
        &--logo {
            .lg-block({
                order: 1;
                margin: 0 15px;
            });
            .sm-block({
                margin: 0 auto;
                width: 100%;
                text-align: center;
            });
            img {
                padding: 10px 0;
                .sm-block({
                   padding-top: 0;
                });
            }
        }
        .main-nav {
            transition: .1s all;
            .lg-block({
                order: 6;
                margin: 10px auto;
            });
            margin-left: 20px;
            font-weight: bold;
            &_item {
                display: inline-block;
                margin: 0 10px;
                transition: .3s all;
                position: relative;
                &:hover {
                    text-decoration: underline;
                }
                .sub {
                    display: none;
                    position: absolute;
                    left: -200px;
                    min-width: 600px;
                    padding: 10px;
                    &_item {
                       float: left;
                        margin: 0 10px;
                        &:hover {
                            text-decoration: underline;
                        }
                    }
                }
                &:hover>ul {
                    display: block;
                    .md-block({
                        display: none;
                    });
                }
            }
        }
        &--search {
            border-left: 1px solid #e0e0e0;
            border-right: 1px solid #e0e0e0;
            text-align: center;
            position: relative;
            padding: 0 10px;
            .lg-block({
                order: 3;
                margin: 0 15px;
            });
            .sm-block({
                  margin-left: 10px;
            });
            button {
                cursor: pointer;
                background: url(../img/serch2.png) no-repeat;
                width: 24px;
                height: 25px;
            }
            .search-active {
                background: white none repeat scroll 0 0;
                border: 1px solid #e0e0e0;
                border-radius: 4px;
                height: 22px;
                padding: 9px;
                width: 279px;
                }    
            .label {
                opacity: 0;
                visibility: hidden;
                position: absolute;
                top: 47px;
                left: -245px;
                transition: .3s all;
                &::after {
                    background: rgba(0, 0, 0, 0) url(../img/po.png) repeat scroll 0 0;
                    content: " ";
                    height: 0 !important;
                    padding: 0 20px 0 3px;
                    position: relative;
                    top: -53px;
                    width: 10px;
                    left: 117px;
                    z-index: 1000;
                }
            }
            .label.active {
                opacity: 1;
                visibility: visible;
            }
        }
        &--basket {
            .lg-block({
                order: 4;
            });
            cursor: pointer;
            background: url(../img/basket.png) no-repeat;
            width: 32px;
            height: 28px;
            padding: 0 10px;
            position: relative;
            top: -5px; left: 15px;
            &::after {
                content: '0';
                font-size: 12px;
                font-weight: bold;
                color: #fff;
                position: relative;
                left: 10px;
            }
        }
        &--money {
            .lg-block({
                order: 5;
            });
            padding: 0 5px;
            margin-left: 5px;
        }
    }
}