.main-footer {
    margin-top: 50px;
    &_wrap {
        border-top: 1px solid #999;
        padding: 35px 0 50px;
        .row-flex();
        justify-content: space-between;
        .md-block({
            flex-direction: column;
            justify-content: center;
            align-items: center;
        });
    }
    &_left {
        margin-left: 30px;
        .md-block({
            margin-left: 0;
        });
        .menu {
            display: none;
            .sm-block({
                display: block;
                color: #00647a;
                margin: 0 auto;
                padding: 5px 0 10px;
                text-align: center;
                &:hover {
                    text-decoration: underline;
                }
            });
        }
        .footer-nav {
            .sm-block({
                display: none;
            });
            margin-bottom: 20px;
            a {
                position: relative;
                color: #00647a;
                padding: 0 5px;
                &:hover {
                    text-decoration: underline;
                }
                &::after {
                    content: '';
                    background-color: #00647a;
                    height: 14px;
                    width: 1px;
                    position: absolute;
                    top: 2px; right: -2px;
                }
                &:last-child {
                    &::after {
                        display: none;
                    }
                }
            }
        }
        .footer-company {
            color: #999;
            .md-block({
                display: block;
                text-align: center;
                margin: 0 auto;
            });
        }
        .footer-map {
            display: block;
            margin-bottom: 25px;
            position: relative;
            padding-left: 25px;
            color: #00647a;
            .md-block({
                padding: 0;
                text-align: center;
                margin: 0 auto 15px;
            });
            &:hover {
                text-decoration: underline;
            }
            &::before {
                content: '';
                position: absolute;
                top: 0; left: 0;
                background: url(../img/sitemap.png) no-repeat;
                width: 21px;
                height: 15px;
                .md-block({
                    display: none;
                });
            }
        }
    }
    &_right {
        .md-block({
            margin-top: 25px;
        });
        .xs-block({
            display: flex;
            flex-direction: column;
            margin: 25px auto 0;
            text-align: center;
        });
        &--tel {
            .md-block({
                text-align: center;
            });
            .xs-block({
                display: block;
            });
            p {
                .xs-block({
                    margin: 5px auto 20px;
                });
                font-size: 25px;
                &::after {
                    top: 5px;
                }
            }
            .call-btn {
                .lg-block({
                    margin: 0;
                    margin-top: 10px;
                    display: block;
                    width: 120px;
                });
                .md-block({
                    margin: 10px auto 0;
                    display: block;
                    width: 120px;
                });
            }
        }
        &--mailaddress {
            margin-top: 20px;
            .row-flex();
            justify-content: space-between;
            .xs-block({
                display: block;
            });
            .mail {
                padding-left: 20px;
                padding-right: 5px;
                position: relative;
                color: #00647a;
                &:hover {
                    text-decoration: underline;
                }
                &::after {
                    content: '';
                    position: absolute;
                    top: 2px; left: 0;
                    background: url(../img/mail.png) no-repeat;
                    width: 15px;
                    height: 13px;
                }
            }
            .address {
                line-height: 1.5;
                .xs-block({
                    margin: 10px auto;
                    padding: 0px 5px;
                });
            }
        }
    }
}