  font-weight: 700;
}

.goods-item_sum--bottom {
  margin-top: 20px;
}

.goods-item_sum--bottom span {
  font-size: 16px;
  font-weight: 700;
  line-height: 25px;
}

.goods-item_sum--bottom li {
  padding-left: 35px;
  margin: 3px 0;
  position: relative;
}

.goods-item_sum--bottom li::after {
  content: '';
  background: url(../img/ok.png) no-repeat;
  width: 24px;
  height: 24px;
  position: absolute;
  top: 0;
  left: 0;
}

.goods-item_sum--bottom li p {
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
}

.goods-item_variant {
  margin-top: 50px;
}

.goods-item_variant--title {
  display: block;
  padding: 2px 0;
  color: #3e3e3e;
  font-size: 16px;
  line-height: 32px;
  background-color: #f5f5f5;
  font-weight: 700;
  padding-left: 10px;
}

.goods-item_variant--wrap {
  display: flex;
  flex-direction: column;
}

.goods-item_variant--item .item_top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #f5f5f5;
  padding: 10px;
}

.goods-item_variant--item .item_top--left p {
  display: inline-block;
  color: #3e3e3e;
  font-size: 14px;
  font-weight: 400;
}

.goods-item_variant--item .item_top--left p:last-child {
  margin-left: 15px;
}

.goods-item_variant--item .item_top--right {
  border-radius: 4px;
  border: 2px solid #fa6b09;
  text-align: center;
  color: #000000;
  font-size: 14px;
  font-weight: 400;
  position: relative;
  padding: 12px 35px 12px 23px;
  cursor: pointer;
}

.goods-item_variant--item .item_top--right::after {
  content: '';
  background: url(../img/detal.png) no-repeat;
  width: 10px;
  height: 5px;
  position: absolute;
  top: 18px;
  right: 20px;
}

.goods-item_variant--item .item_bottom {
  display: flex;
  flex-direction: column;
  background-color: #f5f5f5;
  padding: 20px 20px 0;
}

.goods-item_variant--item .item_bottom--tr {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #e1e1e1;
}

.goods-item_variant--item .item_bottom--td {
  color: #3e3e3e;
  font-size: 14px;
  line-height: 29px;
}

.goods-item_variant--item .item_bottom--td:last-child {
  text-align: right;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
