@media screen and (max-width: 1200px) {
  .main-header_top--tel {
    flex-direction: column;
  }

  .main-header_top--tel .tel {
    padding-top: 7px;
    padding-bottom: 10px;
  }

  .main-header_top--tel .tel::after {
    top: 7px;
  }

  .main-header_bottom--wrap {
    justify-content: center;
  }

  .main-header_bottom--logo {
    order: 1;
    margin: 0 15px;
  }

  .main-header_bottom .main-nav {
    order: 6;
    margin: 10px auto;
  }

  .main-header_bottom--search {
    order: 3;
    margin: 0 15px;
  }

  .main-header_bottom--basket {
    order: 4;
  }

  .main-header_bottom--money {
    order: 5;
  }

  .sidebar {
    width: calc(25% - 15px);
  }

  .content {
    width: calc(75% - 15px);
  }

  .main-footer_right--tel .call-btn {
    margin: 0;
    margin-top: 10px;
    display: block;
    width: 120px;
  }

  .goods-item_picture {
    width: calc(50% - 15px);
  }

  .goods-item_parameter {
    width: calc(50% - 15px);
  }

  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 992px) {
  .main-header_bottom .main-nav_item:hover > ul {
    display: none;
  }

  .main-wrap {
    justify-content: center;
    margin: 20px auto 0;
  }

  .sidebar {
    width: calc(83.33333333333334% - 15px);
  }

  .content {
    width: calc(83.33333333333334% - 15px);
  }

  .content {
    margin-top: 25px;
  }

  .content_service {
    justify-content: center;
  }

  .content_service__item {
    width: calc(100% - 15px);
  }

  .content_slider .slick-prev {
    left: 0px;
  }

  .content_slider .slick-next {
    right: 0px;
  }

  .main-footer_wrap {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .main-footer_left {
    margin-left: 0;
  }

  .main-footer_left .footer-company {
    display: block;
    text-align: center;
    margin: 0 auto;
  }

  .main-footer_left .footer-map {
    padding: 0;
    text-align: center;
    margin: 0 auto 15px;
  }

  .main-footer_left .footer-map::before {
    display: none;
  }

  .main-footer_right {
    margin-top: 25px;
  }

  .main-footer_right--tel {
    text-align: center;
  }

  .main-footer_right--tel .call-btn {
    margin: 10px auto 0;
    display: block;
    width: 120px;
  }

  .goods-item_sum--flex {
    flex-wrap: wrap;
    justify-content: center;
  }

  .goods-item_sum--action {
    margin-top: 35px;
  }

  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 768px) {
  .main-header_bottom--logo {
    margin: 0 auto;
    width: 100%;
    text-align: center;
  }

  .main-header_bottom--logo img {
    padding-top: 0;
  }

  .main-header_bottom--search {
    margin-left: 10px;
  }

  .main-footer_left .menu {
    display: block;
    color: #00647a;
    margin: 0 auto;
    padding: 5px 0 10px;
