  }

  .main-footer_left .menu:hover {
    text-decoration: underline;
  }

  .main-footer_left .footer-nav {
    display: none;
  }

  .goods-item_wrap {
    justify-content: center;
  }

  .goods-item_picture {
    width: calc(83.33333333333334% - 15px);
  }

  .goods-item_parameter {
    width: calc(83.33333333333334% - 15px);
  }

  .goods-item_parameter {
    margin-top: 25px;
  }

  .goods-item_sum--wrap {
    justify-content: center;
  }

  .goods-item_sum--left {
    width: calc(83.33333333333334% - 15px);
  }

  .goods-item_sum--right {
    width: calc(83.33333333333334% - 15px);
  }

  .goods-item_sum--right {
    margin-top: 25px;
  }

  .goods-item_variant--item .item_top--left p:last-child {
    margin-left: 0;
    margin-top: 15px;
  }

  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 480px) {
  .main-header_top--wrap {
