  }

  .main-header_top--regime {
    padding: 10px 30px;
    text-align: center;
  }

  .main-header_top--mail {
    margin-left: 0;
  }

  .main-header_top--tel {
    margin-top: 10px;
  }

  .main-header_top--reg {
    order: -1;
