@import "smart-grid.less";
@import "elements.less";
@import "header.less";
@import "main.less";
@import "footer.less";
@import "goods.less";

.reset();
.container {
    .container();
    overflow: hidden;
}
html {
    font-size: 14px;
}
a {
    text-decoration: none;
    color: inherit;
}
a:hover {
    text-decoration: none;
}
body {
    font-family: 'Arial', sans-serif;
}
ul,li {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
:active, :hover, :focus {
    outline: 0;
    outline-offset: 0;
}
input {
    border: none;
}
button {
    background: none;
    border: none;
}
@blue : #05acd0;
@main : #ff6b09;
