.main-slider {
    cursor: pointer;
}
.slick-dots {
    position: absolute;
    text-align: center;
}
.slick-dots li {
    position: relative;
    display: inline-block;
    border-radius: 50%;
    background: #eee;
    width: 10px;
    height: 10px;
    margin: 0 5px;
    cursor: pointer;
}
.slick-dots li button {
    display: none;
}
.slick-dots li.slick-active {
    background-color: @blue;
}
.slider-nav-thumbnails .slick-slide {
    opacity: 0.5;
}

.slider-nav-thumbnails .slick-slide.slick-active {
    opacity: 1;
    background: green;
}

.btn_mnu {
    width: 50px;
    height: 50px;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    cursor: pointer;
    position: relative;
    opacity: 0;
    visibility: hidden;
    transition: .3s all;
    .lg-block({
       
    });
}
.btn_row {
    height: 2px;
    width: 60%;
    margin: 2px 0;
    background-color: #000;
    position: relative;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
}
.btn_mnu.active .btn_row:first-child {
    -webkit-transform: rotate(-135deg);
    -ms-transform: rotate(-135deg);
    -o-transform: rotate(-135deg);
    transform: rotate(-135deg);
    top: 6px;
}
.btn_mnu.active .btn_row:last-child {
    -webkit-transform: rotate(-225deg);
    -ms-transform: rotate(-225deg);
    -o-transform: rotate(-225deg);
    transform: rotate(-225deg);
    top: -6px;
}
.btn_mnu.active .btn_row:nth-child(2) {
    opacity: 0;
}
.minus, .plus {
    cursor: pointer;
    text-align: center;
    vertical-align:middle;
    color: #00627b;
    padding: 0 2px;
    position: relative;
    color: #fa6b09;
    font-size: 20px;
    font-weight: 700;
}
.minus {
    left: 35px;
}
.plus {
    left: -40px;
}
.numb {
    border-radius: 4px;
    border: 2px solid #fa6b09;
    text-align: center;
    padding: 12px 0;
    font-weight: 700;
}

.breadcrumbs {
    &_li {
        display: inline;
        position: relative;
        padding: 0 15px 0 10px;
        &:first-child {
            padding-left: 0;
        }
        &:last-child::after {
            display: none;
        }
        &::after {
            position: absolute;
            top: -5px; right: 0;
            content: "»";
            color: #ff6b09;
            font-size: 20px;
            font-weight: bold;
        }
        a {
            color: #00647a;
            font-size: 16px;
            font-weight: bold;
            line-height: 1.5;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}
.breadcrumbs_li.active a {
    text-decoration: underline;
}
.btn-up {
    cursor: pointer;
    position: fixed;
    bottom: -50px;
    right: 50px;
    width: 50px;
    height: 50px;
    background-position: center center;
    background-repeat: no-repeat;
    background-image: url(../img/up-arrow.png);
    opacity: 0.8;
    background-color: #afe4f0;
    z-index: 100;
    transition: .1s;
    border-radius: 7px;
}

.btn-up:hover {
    background-color: #e5501f;
    opacity: 1;
}
.btn-up.active {
    bottom: 50px;
}
.goods-item_complect--item.active {
    background: @main;
    color: #fff;
}