
.goods-item {
    &_title {
        margin-top: 5px;
        font-size: 21px;
        line-height: 32px;
        color: @main;
        font-weight: bold;
        padding: 10px 0;
    }
    &_wrap {
        margin-top: 15px;
        .row-flex();
        .sm-block({
            justify-content: center;
        });
    }
    &_picture {
       .col();
        .size(5);
        .size-lg(6);
        .size-sm(10);
        .slides_small img{
            height: 360px;
            width: auto;
            text-align: center;
            margin: 20px auto 0;
        }
        .flexslider {
            margin: 0;
        }
        .flex-active-slide {
            width: 360px;
            height: 420px;
        } 
        .flexslider:hover .flex-direction-nav .flex-prev {
            opacity: 1;
            left: 10px;
        }
        .flexslider:hover .flex-direction-nav .flex-next {
            opacity: 1;
            right: 10px;
        }
        .flex-control-thumbs img {
            width: 25px;
            height: 49px; 
            margin: auto;
        }
        .flex-control-thumbs {
            display: flex;
            justify-content: flex-start;
        }
        .flex-control-thumbs li {
            border: 1px solid #f5f5f5;
            background-color: #ffffff;
        }
        .flex-prev,
        .flex-next {
            opacity: 1;
            display: inline-block;
            width: 35px;
            height: 35px;
            z-index: 10;
            font-size: 0;
            margin: 0;
            top: 35%;
            &::after,
            &::before {
                content: '';
                display: none;
            }
        }
        .flex-prev {
            background: url(../img/flex-prev.png) no-repeat;
            left: 10px;
        }
        .flex-next {
            background: url(../img/flex-next.png) no-repeat;
            right: 10px;
        }
    }
    &_parameter {
        .col();
        .size(7);
        .size-lg(6);
        .size-sm(10);
        .sm-block({
            margin-top: 25px;
        });
        &--title {
            display: block;
            padding: 2px 0;
            color: #3e3e3e;
            font-size: 16px;
            line-height: 32px;
            text-align: center;
            background-color: #f5f5f5;
            font-weight: 700;
        }
        &--wrap {
            display: flex;
            flex-direction: column;
        }
        &--tr {
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid #e1e1e1;
        }
        &--td {
            color: #3e3e3e;
            font-size: 14px;
            line-height: 29px;
            &:last-child {
                text-align: right;
            }
            .xs-block({
                font-size: 12px; 
            });
        }
    }
    &_complect {
        width: 85%;
        margin-top: 30px;
        &--title {
            display: block;
            padding: 2px 0;
            padding-left: 10px;
            color: #3e3e3e;
            font-size: 16px;
            line-height: 32px;
            background-color: #f5f5f5;
            font-weight: 700;
        }
        &--wrap {
            margin-top: 10px;
        }
        &--item {
            cursor: pointer;
            border-radius: 4px;
            border: 2px solid #fa6b09;
            font-weight: bold;
            padding: 15px 22px;
            display: inline-block;
            margin: 5px;
            text-align: center;
            transition: .3s all;
            &:hover {
                background: @main;
                color: #fff;
            }
        }
        &--ul {
            margin-top: 10px;
        }
        &--li {
            margin: 5px;
            display: inline-block;
        }
        &--input {
            display: none;
        }
        &--label {
            display: inline-block;
            cursor: pointer;
            border-radius: 4px;
            border: 2px solid #fa6b09;
            font-weight: bold;
            padding: 15px 22px;
            text-align: center;
            transition: .3s all;
        }  
        input[type="radio"]:checked+label {
            background: @main;
            color: #fff;
        }
        input[type="radio"]+label {
            
        } 
    }
    &_sum {
        margin-top: 25px;
        &--wrap {
            .row-flex();
            align-items: flex-start;
            .sm-block({
                justify-content: center;
            });
        }
        &--left {
            .col();
            .size(7.5);
            .size-sm(10);
        }
        &--right {
            .col();
            .size(4.5);
            .size-sm(10);
            border: 1px solid #3e3e3e;
            .sm-block({
                margin-top: 25px;
            });
        }
        &--title {
            display: block;
            padding: 2px 0;
            color: #3e3e3e;
            font-size: 16px;
            line-height: 32px;
            background-color: #f5f5f5;
            font-weight: 700;
            text-align: center;
        }
        &--input {
            position: relative;
            margin: 20px auto 0;
            text-align: center;
        }
        &--flex {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            margin-top: 15px;
            .md-block({
                flex-wrap: wrap;            
                justify-content: center;
            });
        }
        &--sale,
        &--action {
            text-align: center;
            span {
                color: #3e3e3e;
                font-size: 18px;
                font-weight: 700;
                line-height: 30px;
                display: inline-block;
                margin-bottom: 5px;
            }
            p {
                display: block;
                margin-top: 20px;
                color: #3e3e3e;
                font-size: 18px;
                font-weight: 400;
            }
            .blue {
                color: #05acd0;
                font-size: 40px;
                font-weight: 700;
                text-transform: uppercase;
            }
            .blue_time {
                color: #05acd0;
                font-size: 24px;
                font-weight: 700;
            }
        }
        &--action {
            .md-block({
                margin-top: 35px;
            });
        }
        &--btn {
            border-radius: 4px;
            background-color: #fa6b09;
            text-align: center;
            padding: 11px 21px;
            color: #ffffff;
            font-size: 14px;
            font-weight: 700;
            display: inline-block;
            transition: .3s all;
            border: 2px solid @main;
            &:hover {
                border: 2px solid @main;
                color: @main;
                background-color: #fff;
            }
        }
        &--right {
            padding: 20px;
            color: #3e3e3e;
        }
        &--top {
            p {
                margin: 5px 0;
                font-size: 14px;
                font-weight: 400;
                line-height: 25px;
                &:first-child {
                    font-size: 16px;
                    font-weight: 700;
                }
            }
        }
        &--bottom {
            margin-top: 20px;
            span {
                font-size: 16px;
                font-weight: 700;
                line-height: 25px;
            }
            li {
                padding-left: 35px;
                margin: 3px 0;
                position: relative;
                &::after {
                    content: '';
                    background: url(../img/ok.png) no-repeat;
                    width: 24px;
                    height: 24px;
                    position: absolute;
                    top: 0; left: 0;
                    
                }
                p {
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 30px;
                }
            }
        }
    }
    &_variant {
        margin-top: 50px;
        &--title {
            display: block;
            padding: 2px 0;
            color: #3e3e3e;
            font-size: 16px;
            line-height: 32px;
            background-color: #f5f5f5;
            font-weight: 700;
            padding-left: 10px;
        }
        &--wrap {
            display: flex;
            flex-direction: column;
        }
        &--item {
            .item_top {
                display: flex;
                justify-content: space-between;
                align-items: center;
                border-bottom: 1px solid #f5f5f5;
                padding: 10px;
                
                &--left {
                    p {
                        display: inline-block;
                        color: #3e3e3e;
                        font-size: 14px;
                        font-weight: 400;
                        &:last-child {
                            margin-left: 15px;
                            .sm-block({
                                margin-left: 0;
                                margin-top: 15px;
                            });
                        }
                    }
                }
                &--right {
                    border-radius: 4px;
                    border: 2px solid #fa6b09;
                    text-align: center;
                    color: #000000;
                    font-size: 14px;
                    font-weight: 400;
                    position: relative;
                    padding: 12px 35px 12px 23px;
                    cursor: pointer;
                    &::after {
                        content: '';
                        background: url(../img/detal.png) no-repeat;
                        width: 10px;
                        height: 5px;
                        position: absolute;
                        top: 18px;
                        right: 20px;
                    }
                }
            }
            .item_bottom  {
                    display: flex;
                    flex-direction: column;
                    background-color: #f5f5f5;
                    padding: 20px 20px 0;
                &--tr {
                    display: flex;
                    justify-content: space-between;
                    border-bottom: 1px solid #e1e1e1;
                }
                &--td {
                    color: #3e3e3e;
                    font-size: 14px;
                    line-height: 29px;
                    &:last-child {
                        text-align: right;
                    }
                    .xs-block({
                       font-size: 12px; 
                    });
                }
            }
            
        }
        
    }
}


