.main-wrap {
    .row-flex();
    align-items: flex-start;
    margin-top: 20px;
    .md-block({
        justify-content: center;
        margin: 20px auto 0;
    });
}
.sidebar {
    .col();
    .size(2.5);
    .size-lg(3);
    .size-md(10);
    .size-xs(12);
    color: #00647a;
    border: 1px solid @blue;
}
.content {
    .col();
    .size(9.5);
    .size-lg(9);
    .size-md(10);
    .size-xs(12);
    .md-block ({
        margin-top: 25px;
    });
    
    &_service {
        .row-flex();
        margin-top: 35px;
        .md-block({
            justify-content: center;
        });
        
        &__item {
            .col-padding();
            .size-md(12);
        }
        &__blue {
            .size(5.8);
            background-color: @blue;
            color: #fff;
            padding: 40px;
            text-align: center;
            .xs-block({
                padding: 25px 20px;
            });
        }
        &__title {
            font-size: 24px;
            font-weight: 700;
            margin: 20px 0 10px;
        }
        &__descr {
            font-size: 16px;
            line-height: 25px;
        }
        &__span {
            margin: 60px auto 25px;
            font-size: 18px;
            display: inline-block;
            text-align: center;
        }
        &__ul {
            margin-left: 20px;
        }
        &__li {
            text-align: left;
            max-width: 320px;
            padding-left: 55px;
            position: relative;
            margin: 15px 0;
            p {
                font-size: 16px;
                font-weight: 400;
                line-height: 30px;
            }
            &::after {
                content: '';
                position: absolute;
                top: 0; left: 0;
            }
            &:first-child {
                &::after {
                    background: url(../img/Forma_1.png) no-repeat;
                    width: 41px;
                    height: 30px;
                } 
            }
            &:last-child {
                &::after {
                    background: url(../img/Forma_2.png) no-repeat;
                    width: 30px;
                    height: 30px;
                } 
            }
        }
       
        &__gray {
            .size(5.2);
            text-align: center;
            background-color: #eee;
            padding: 35px 0 30px;
            img {
                padding: 10px 5px 15px;   
            }
        }
        &__name {
            font-size: 17px;
            line-height: 1.5;
        }
        &__consult {
            font-weight: bold;
            font-size: 17px;
            span {
                text-transform: uppercase;
                color: #ff6b09;
            }
        }
        &__input {
            border: 1px solid #e0e0e0;
            border-radius: 3px;
            display: block;
            font-size: 15px;
            height: 42px;
            margin: 10px auto;
            padding-left: 10px;
            text-align: center;
            width: 255px;
        }
        &__submit {
            cursor: pointer;
            background-color: @main;
            border-radius: 3px;
            display: block;
            font-size: 14px;
            height: 42px;
            margin: 0 auto;
            width: 267px;
            color: #fff;
            transition: .1s all;
            &:hover {
                opacity: .8;
            }
        }
    }
    &_slider {
        margin-top: 50px;
        padding: 0 50px;
        
        .slick-prev, .slick-next {
            background: #05acd0;
            border-radius: 35px;
            width: 35px;
            height: 35px;
            color: #fff;
            &::before {
                font-size: 25px;
                line-height: 25px;
                font-weight: bold;
                text-align: center;
                position: relative;
                top: -2px;
            }
        }
        .slick-prev {
            left: -10px;
            .md-block({
               left: 0px; 
            });
        }
        .slick-next {
            right: 20px;
            .md-block({
                right: 0px; 
            });
        }
    }
}
